import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';

// Font awesome icons.
// TODO: Extract into it's own file somehow
import {
  faBars,
  faTimes,
  faPhone,
  faPaperPlane,
  faSearchPlus,
  faChevronCircleRight,
  faChevronCircleLeft,
  faTimesCircle,
  faSpinner
} from '@fortawesome/free-solid-svg-icons';
import { faWhatsapp, faFacebookSquare } from '@fortawesome/free-brands-svg-icons';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './components/app/app.component';
import { HeaderComponent } from './components/header/header.component';
import { HomeComponent } from './components/home/home.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { FooterComponent } from './components/footer/footer.component';
import { ServicesComponent } from './components/services/services.component';
import { ContactusComponent } from './components/contactus/contactus.component';
import { AboutusComponent } from './components/aboutus/aboutus.component';
import { ImgSpotlightComponent } from './components/img-spotlight/img-spotlight.component';

library.add(
  faBars,
  faTimes,
  faPhone,
  faWhatsapp,
  faPaperPlane,
  faFacebookSquare,
  faSearchPlus,
  faChevronCircleLeft,
  faChevronCircleRight,
  faTimesCircle,
  faSpinner
);

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HomeComponent,
    PageNotFoundComponent,
    FooterComponent,
    ServicesComponent,
    ContactusComponent,
    AboutusComponent,
    ImgSpotlightComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FontAwesomeModule,
    FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
