import { Component, Input, Output, EventEmitter, OnChanges, OnInit, OnDestroy, SimpleChanges } from '@angular/core';
import { Observable } from 'rxjs';

@Component({
  selector: 'sp-img-spotlight',
  templateUrl: './img-spotlight.component.html',
  styleUrls: ['./img-spotlight.component.less']
})
export class ImgSpotlightComponent implements OnInit, OnChanges, OnDestroy {
  @Input() image?: string;
  @Input() scrollThroughImages?: boolean;

  @Output() scrollImage = new EventEmitter<string>();
  @Output() exitSpotlight = new EventEmitter<void>();

  imgSrc = '';
  keyboardInputSubscription$;

  ngOnInit() {
    this.startKeyboardListener();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.image) this.imgSrc = changes.image.currentValue;
  }

  ngOnDestroy() {
    this.keyboardInputSubscription$.unsubscribe();
  }

  private startKeyboardListener() {
    const keyboardInput$: Observable<KeyboardEvent> = new Observable((observer) => {
      document.addEventListener('keydown', event => observer.next(event));
    });

    this.keyboardInputSubscription$ = keyboardInput$
    .subscribe(keydown => {
      switch (keydown.keyCode) {
        case 37:
          this.scrollImage.emit('left');
          break;
        case 39:
          this.scrollImage.emit('right');
          break;
        case 27:
          this.exitSpotlight.emit();
          break;
      }
    });
  }
}
